.navbar-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.navbar-desktop-menu {
  width: 100%;
  height: 10vh;
  display: flex;
  padding: 32px 48px;
  align-items: center;
}
.navbar-logo {
  max-width: 30vw;
  height: auto;
}
.navbar-mobile-logo {
  height: calc(10vh - 32px);
}
.navbar-nav {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}
.navbar-item {
  color: var(--dl-color-darkmode-100);
}
.navbar-mobile-menu {
  max-width: 100%;
  height: 10vh;
  display: none;
  padding: 16px 32px;
}
.navbar-container1 {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.navbar-logo {
  transition: 0.3s;
}
.navbar-logo:hover {
  transform: scale(1.05);
}
.navbar-icon {
  height: 100%;
  align-self: center;
}
.navbar-burger-menu {
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  z-index: 100;
  position: fixed;
  transform: translateX(-100%);
  transition: 0.5s;
  flex-direction: column;
  background-color: #ffffff;
}
.navbar-container2 {
  width: 100%;
  height: 10vh;
  display: flex;
  padding: 16px 32px;
  margin-bottom: 32px;
  justify-content: space-between;
}
.navbar-logo1 {
  transition: 0.3s;
}
.navbar-logo1:hover {
  transform: scale(1.05);
}
.navbar-icon2 {
  height: 100%;
}
.navbar-container3 {
  width: 100%;
  height: 90vh;
  display: flex;
  padding: 0 32px;
  flex-direction: column;
}

@media (max-width: 991px) {
  .navbar-desktop-menu {
    display: none;
  }
  .navbar-mobile-menu {
    display: flex;
  }
}

@media (max-width: 767px) {
  .navbar-item {
    margin-left: var(--dl-space-space-unit);
  }
}
